import React from 'react'
import './App.css'
const Bubble = (props) => {
    return (
        <div className="userBubble" style={{backgroundColor: props.color, color:'white', float: props.float}}>
            <span style={{fontSize:'smaller', fontWeight:'bold', wordWrap: 'break-word'}}>{props.message.text}</span>
            <br />
            <span style={{fontSize:'10px'}}>from: {props.message.from} @ {props.message.time}</span>
        </div>
    )
}
export default Bubble


