import React from 'react'
import ReactDOM from 'react-dom'
import ListItem from 'material-ui/List/ListItem'
import Bubble from './bubble'
import MessageBlock from './messageblock'
class UserBubble extends React.Component {
    componentDidMount = () => {
        let userDOM = ReactDOM.findDOMNode(this);
        userDOM.focus();
        userDOM.scrollIntoView();
        userDOM.blur()
    };
    render() {
        return (
            <div>
                <ListItem ref='message' style={{textAlign: 'left', marginBottom: '5px'}} disabled={true}>
                    <Bubble message={this.props.message} color={this.props.message.color} shifted={this.props.message.shifted} float={this.props.message.float}/>
                    <MessageBlock />
                    <div style={{height: '10px'}}> </div>
                </ListItem>
                &nbsp;
            </div> )
    }
}
export default UserBubble


