import React from 'react'
const MessageBlock = (props) => {
    return (
        <div
            style={{content:"", /* triangle */
                position:'absolute',
                bottom:'-10px', /* value = - border-top-width - border-bottom-width */
                left: '50px', /* controls horizontal position */}}
        />
    )
};
export default MessageBlock;


