import React from 'react'
import ListItem from 'material-ui/List/ListItem'
const uuidv4 = require('uuid/v4');
class RoomsActiveList extends React.Component {
    render() {
        return (
            this.props.rooms.map(item => {
                return (
                    <ListItem insetChildren={true} key={uuidv4()} primaryText={item.room} disabled={true}/>
                )
            })
        )
    }
}
export default RoomsActiveList



