import React from 'react'
import Icon from 'material-ui/svg-icons/action/list'
import {Toolbar, ToolbarTitle} from 'material-ui/Toolbar'
import IconButton from 'material-ui/IconButton'
const TopBar = (props) => {
    const iconStyles = {height: 50, width: 50, marginTop: -10},
        onIconClicked = () => props.viewDialog(); // notify the parent
    return (
        <Toolbar style={{backgroundColor: '#000', color: 'white', marginBottom:20}}>
            <ToolbarTitle style={{fontFamily: 'Coolvetica'}} text='FakeIRC#'/>
            <IconButton tooltip="We're not actually an IRC"
                        tooltipPosition="bottom-left"
                        onClick={onIconClicked}
                        iconStyle={iconStyles}
            >
                <Icon style={iconStyles} color='white' />
            </IconButton>
        </Toolbar>
    )
};
export default TopBar



