import React from 'react';
import io from 'socket.io-client';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {Card, CardText} from 'material-ui/Card';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import Chip from 'material-ui/Chip';
import Avatar from 'material-ui/Avatar';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Drawer from 'material-ui/Drawer';
import List from 'material-ui/List/List';
import ListItem from 'material-ui/List/ListItem';
import logo from './fakeircblack-logo.png';
//import bloop from './bloop.mp3'
import UsersLogo from 'material-ui/svg-icons/action/face';
import RoomsLogo from 'material-ui/svg-icons/communication/forum';
import UserBubbleList from './userbubblelist';
import UsersActiveList from './usersactive';
import RoomsActiveList from './roomsactive';
import TopBar from './topbar';
import './App.css';

const uuidv4 = require('uuid/v4');
//const audio = new Audio(bloop);

class ServerTestComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            roomsSelected: '',
            messages: [],
            rooms: [],
            users: [],
            color: '',
            isVisible: 'block',
            isClickable: true,
            joined: false,
            msg: '',
            stated: false,
            message: '',
            roomName: '',
            chatName: '',
            socket: null,
            down: false,
            update: '',
            typing: '',
            open: false,
            errorExists: ''
        }
    }

    componentDidMount = async () => {
        // connect to server
        const socket = await io.connect({'forceNew': true});
        await this.setState({socket: socket});
        socket.on('nameexists', this.onNameExists);
        socket.on('welcome', this.onWelcome);
        socket.on('joined', this.onJoined);
        socket.on('currentRoomsAndUsers', this.onJoinedSuccess);
        socket.on('messageTyping', this.onTyping);
        socket.on('messageSent', this.onMessage);
    };
    // when we join a room
    onWelcome = (dataFromServer) => {
        //console.log(`From: ${dataFromServer.from}\nRoom: ${dataFromServer.room}\nMessage: ${dataFromServer.text}\nSocket Id: ${dataFromServer.id}\nColour: ${dataFromServer.color}\nTime: ${dataFromServer.createdAt}`)
        this.state.messages.push({
            id: uuidv4(),
            text: dataFromServer.text,
            from: dataFromServer.from,
            color: dataFromServer.color,
            time: dataFromServer.createdAt,
            shifted: (window.innerWidth - 415),
            float: 'left'
        });
        //console.log(this.state.messages)
        this.setState({id: 'updated'});
    };
    onNameExists = () => {
        this.setState({errorExists: 'name in use, enter unique name (auto lowercase)'});
        this.setState({isVisible: 'block'});
        this.setState({joined: false});
    };
    // when someone joins the room we're in
    onJoined = (dataFromServer) => {
        //console.log(`From: ${dataFromServer.from}\nRoom: ${dataFromServer.room}\nMessage: ${dataFromServer.text}\nColour: ${dataFromServer.color}\nTime: ${dataFromServer.createdAt}`)
        this.state.messages.push({
            id: uuidv4(),
            text: dataFromServer.text,
            from: dataFromServer.from,
            color: dataFromServer.color,
            time: dataFromServer.createdAt,
            shifted: (window.innerWidth - 415),
            float: 'left'
        });
        //console.log(this.state.messages)
        this.setState({id: 'updated'})
    };
    onJoinedSuccess = (dataFromServer) => {
        this.setState({rooms: dataFromServer.currentRooms});
        this.setState({users: dataFromServer.currentUsers});
        this.setState({update: 'updated'});
        this.setState({update: 'updated'});
        //console.log(`Rooms: ${dataFromServer.rooms}`)
        //console.log(`Users: ${dataFromServer.users}`)
    };
    onTyping = async (dataFromServer) => {
        console.log(dataFromServer.typingUsers.length);
        if(dataFromServer.typingUsers.length > 2) {
            await this.setState({typing: `multiple users are typing...`});
        }
        else {
            await this.setState({typing: `${dataFromServer.name} is typing...`});
        }
        if(dataFromServer.msg.length <= 0) {
            let index = await dataFromServer.typingUsers.indexOf({user: dataFromServer.userName});
            if(index !== -1) {
                await dataFromServer.typingUsers.splice(index, 1);
                await this.state.socket.emit('typing', {userName: this.state.chatName, message: this.state.message, typedUsers: dataFromServer.typingUsers});
            }
            else {
                if(dataFromServer.typingUsers.length > 0 && dataFromServer.typingUsers.length <= 2) {
                    await this.setState({typing: ''});
                }
                else {
                    await this.setState({typing: `multiple users are typing...`});
                }
            }
        }
        this.setState({update: 'updated'});
    };
    onMessage = async (dataFromServer) => {
        if (this.state.socket.id !== dataFromServer.id) {
            this.state.messages.push({
                id: uuidv4(),
                text: dataFromServer.text,
                from: dataFromServer.from,
                color: dataFromServer.color,
                time: dataFromServer.createdAt,
                shifted: (window.innerWidth - 415),
                float: 'left'
            });
            //audio.play()
        }
        else {
            this.state.messages.push({
                id: uuidv4(),
                text: dataFromServer.text,
                from: dataFromServer.from,
                color: dataFromServer.color,
                time: dataFromServer.createdAt,
                shifted: (window.innerWidth - 285),
                float: 'right'
            })
        }
        //console.log(this.state.messages)
        this.setState({typing: ''});
        this.setState({update: 'updated'});
    };
    // when we join a room
    handleJoin = async () => {
        await this.state.socket.connect();
        await this.setState({errorExists: ''});
        await this.setState({joined: true});
        await this.setState({isVisible: 'none'});
        await this.state.socket.emit('join', {chatName: this.state.chatName, roomName: this.state.roomName});
    };
    // handler for name TextField entry
    handleNameChange = async (e) => {
        await this.setState({chatName: e.target.value});
        await this.handleDisable();
    };
    handleRoomChange = async (e) => {
        await this.setState({roomName: e.target.value});
        await this.handleDisable();
    };
    handleMessageChange = async (e) => {
        await this.setState({message: e.target.value});
        await this.state.socket.emit('typing', {userName: this.state.chatName, message: this.state.message});
        await this.setState({update: 'updated'});
    };
    handleNewMessage = async (e) => {
        if (e.keyCode === 13) {
            e.preventDefault();
            await this.setState({stated: true});
            if (this.state.message.length > 0 && this.state.stated === true) {
                await this.setState({stated: false});
                this.state.socket.emit('createMessage', {
                    from: this.state.chatName,
                    text: this.state.message
                }, (err) => {
                })
            }
            await this.setState({message: ''});
            await this.setState({typing: ''});
            await this.setState({update: 'updated'});
        }
    };
    handleOpenDialog = () => this.setState({open: true});
    handleDisable = async () => {
        if (this.state.chatName.length > 0 && this.state.roomName.length > 0)
            await this.setState({isClickable: false});
        else if (this.state.chatName.length <= 0 || this.state.roomName.length <= 0)
            await this.setState({isClickable: true});
        await this.setState({roomsSelected: ''});
        await this.setState({update: 'updated'});
    };
    onBack = async () => {
        await this.setState({isVisible: 'block'});
        await this.setState({joined: false});
        this.state.messages = [];
        await this.state.socket.emit('isLeaving', {chatName: this.state.chatName, roomName: this.state.roomName});
        await this.setState({update: 'updated'});
        await this.setState({update: 'updated'});
        window.location.reload();
    };
    handleSelect = async (e, i, v) => {
        await this.setState({roomsSelected: v});
        await this.setState({roomName: v});
        await this.handleDisable();
    };

    menuItems(rooms) {
        if (rooms !== undefined) {
            return rooms.map((room) => (
                <MenuItem
                    key={room.key}
                    insetChildren={true}
                    checked={this.state.rooms.indexOf(room.key) > -1}
                    value={room.room}
                    primaryText={room.room}
                />
            ));
        }
    }

    render() {
        return (
            <MuiThemeProvider>
                <div>
                    <div>
                        <TopBar viewDialog={this.handleOpenDialog}/>
                        <Drawer
                            docked={false}
                            disable={true}
                            width={'80%'}
                            open={this.state.open}
                            onRequestChange={(open) => this.setState({open})}
                        >
                            <MenuItem>
                                <div style={{textAlign: 'center', paddingTop: '20px'}}>
                                    <img src={logo} className="App-logo" alt="logo"/>
                                </div>
                                <List style={{height: '500px', overflowY: 'auto'}}>
                                    <ListItem
                                        primaryText="Users"
                                        leftIcon={<UsersLogo/>}
                                        initiallyOpen={false}
                                        primaryTogglesNestedList={true}
                                        nestedItems={[
                                            <UsersActiveList key={uuidv4()} users={this.state.users}/>
                                        ]}
                                    />
                                    <ListItem
                                        primaryText="Rooms"
                                        leftIcon={<RoomsLogo/>}
                                        initiallyOpen={false}
                                        primaryTogglesNestedList={true}
                                        nestedItems={[
                                            <RoomsActiveList key={uuidv4()} rooms={this.state.rooms}/>
                                        ]}
                                    />
                                </List>
                            </MenuItem>
                        </Drawer>
                    </div>
                    <Card style={{
                        paddingLeft: '10px',
                        paddingRight: '10px',
                        height: '0px',
                        display: this.state.isVisible
                    }}>
                        <div className="App-logo">
                            <img src={logo} className="App-logo" alt="logo"/>
                        </div>
                        <Card style={{margin: '10px'}}>
                            <CardText style={{textAlign: 'center'}}>
                                <h1>Sign In</h1>
                                <TextField
                                    hintStyle={{width: '250px', textAlign: 'center'}}
                                    inputStyle={{textAlign: 'center', fontSize: '20px', tabIndex: '1'}}
                                    hintText='Enter unique name'
                                    value={this.state.chatName}
                                    onChange={this.handleNameChange}
                                /><br/>
                                <p>{this.state.errorExists}</p>
                            </CardText>
                        </Card>
                        <Card style={{margin: '10px', marginTop: '20px'}}>
                            <CardText style={{textAlign: 'center'}}>
                                <h2>Join existing or new room</h2>
                                <SelectField
                                    hintStyle={{width: '250px', textAlign: 'center'}}
                                    hintText="select a room or enter one"
                                    value={this.state.roomsSelected}
                                    onChange={this.handleSelect}>
                                    {this.menuItems(this.state.rooms)}
                                </SelectField>
                                <br/>
                                <TextField
                                    hintStyle={{width: '250px', textAlign: 'center'}}
                                    inputStyle={{textAlign: 'center', fontSize: '20px', tabIndex: '2'}}
                                    hintText='Enter room name'
                                    value={this.state.roomName}
                                    onInput={this.handleRoomChange}
                                />
                                <br/>
                                <RaisedButton
                                    style={{marginTop: '20px', tabIndex: '3'}}
                                    label="join room"
                                    labelColor={'#fff'}
                                    backgroundColor={'#000'}
                                    onClick={this.handleJoin}
                                    disabled={this.state.isClickable}
                                />
                            </CardText>
                        </Card>
                    </Card>
                    {this.state.joined &&
                    <Card style={{paddingLeft: '15px', paddingRight: '10px', height: '0px'}}>
                        <Chip style={{padding: '5px'}} backgroundColor='#999' labelColor='#fff'
                              onRequestDelete={this.onBack}>
                            <Avatar size={32} color='#000' backgroundColor='#fff'>
                                #
                            </Avatar>
                            {this.state.roomName}
                        </Chip>
                        <Card style={{margin: '10px'}}>
                            <div className='usersList'>
                                <UserBubbleList messages={this.state.messages}/>
                            </div>
                        </Card>
                        <Card style={{marginLeft: '10px', marginTop: '10px', marginRight: '10px'}}>
                            <form onKeyDown={this.handleNewMessage}>
                                <TextField
                                    hintStyle={{
                                        marginLeft: '10px',
                                        marginRight: '10px',
                                        width: (window.innerWidth - 80)
                                    }}
                                    inputStyle={{
                                        marginLeft: '10px',
                                        marginRight: '10px',
                                        width: (window.innerWidth - 80)
                                    }}
                                    fullWidth={true}
                                    multiLine={true}
                                    rowsMax={2}
                                    hintText='Type a message...'
                                    value={this.state.message}
                                    onChange={this.handleMessageChange}
                                />
                                <p className='typingMsg'>{this.state.typing}</p>
                            </form>
                        </Card>
                    </Card>
                    }
                    <br/>
                </div>
            </MuiThemeProvider>
        )
    }
}

export default ServerTestComponent


